import React, { useState, useRef, useEffect } from 'react';
import './MoodChat.css';
import { getFullMoodAnalysis } from '../utils/moodDetector';

function MoodChat({ onMoodDetected }) {
  const [messages, setMessages] = useState([
    { 
      sender: 'bot', 
      text: 'Hi there! I\'m Moodify\'s mood assistant. Tell me how you\'re feeling today, and I\'ll help find music that matches your mood.',
      timestamp: new Date()
    }
  ]);
  
  const [userInput, setUserInput] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [conversationStage, setConversationStage] = useState(0);
  const [moodAnalysisState, setMoodAnalysisState] = useState(null);
  const messagesEndRef = useRef(null);
  
  // Reset chat when component mounts or remounts
  useEffect(() => {
    setMessages([
      { 
        sender: 'bot', 
        text: 'Hi there! I\'m Moodify\'s mood assistant. Tell me how you\'re feeling today, and I\'ll help find music that matches your mood.',
        timestamp: new Date()
      }
    ]);
    setConversationStage(0);
    setMoodAnalysisState(null);
    
    // Focus the input field when component mounts
    const inputField = document.querySelector('.mood-chat input');
    if (inputField) {
      inputField.focus();
    }
  }, []);

  // Automatic scrolling to bottom on new messages
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };
  
  // Handle user message submission
  const handleUserMessageSend = (e) => {
    e.preventDefault();
    
    if (!userInput.trim()) return;

    // Get the current stage to determine behavior
    const currentStage = conversationStage;
    
    // Add user's message to chat
    const userMessage = {
      sender: 'user',
      text: userInput,
      timestamp: new Date()
    };
    
    setMessages(prev => [...prev, userMessage]);
    setUserInput('');
    
    // Simulate bot is typing
    setIsTyping(true);
    
    if (currentStage === 0) {
      // Initial stage - analyze mood from first user message
      const moodAnalysis = getFullMoodAnalysis(userInput);
      console.log('Mood Analysis:', moodAnalysis);
      setMoodAnalysisState(moodAnalysis);
      
      // After a delay, show first response
      setTimeout(() => {
        const botResponses = generateBotResponses(moodAnalysis);
        
        setMessages(prev => [...prev, {
          sender: 'bot',
          text: botResponses[0],
          timestamp: new Date()
        }]);
        
        // Move to next conversation stage
        setConversationStage(1);
        setIsTyping(false);
      }, 1200);
    } 
    else if (currentStage === 1) {
      // Second stage - provide more insights about the mood
      setTimeout(() => {
        const botResponses = generateBotResponses(moodAnalysisState);
        
        setMessages(prev => [...prev, {
          sender: 'bot',
          text: botResponses[1],
          timestamp: new Date()
        }]);
        
        setTimeout(() => {
          // Ask if they want music recommendations
          setMessages(prev => [...prev, {
            sender: 'bot',
            text: "Would you like me to suggest some music that matches how you're feeling?",
            timestamp: new Date()
          }]);
          
          // Move to next stage
          setConversationStage(2);
          setIsTyping(false);
        }, 1000);
      }, 1200);
    }
    else if (currentStage === 2) {
      // Final stage - check for affirmative response and show playlists
      const positiveResponses = ['yes', 'yeah', 'yep', 'sure', 'ok', 'okay', 'please', 'yup', 'y'];
      const isAffirmative = positiveResponses.some(word => 
        userInput.toLowerCase().includes(word) || 
        userInput.toLowerCase() === word
      );
      
      setTimeout(() => {
        // If user agrees or says anything positive-ish
        if (isAffirmative) {
          setMessages(prev => [...prev, {
            sender: 'bot',
            text: "Great! I'll curate some playlists that match your emotional state. Give me just a moment...",
            timestamp: new Date()
          }]);
          
          // Short delay then pass the mood analysis to parent for playlist generation
          setTimeout(() => {
            if (onMoodDetected && moodAnalysisState) {
              onMoodDetected(moodAnalysisState.primaryMood, 'chat', moodAnalysisState);
            } else {
              console.error('Either onMoodDetected prop is missing or mood analysis failed');
            }
            setIsTyping(false);
            // Reset conversation stage for future interactions
            setConversationStage(3); // Final stage - conversation complete
          }, 1500);
        } else {
          // If user doesn't want music recommendations
          setMessages(prev => [...prev, {
            sender: 'bot',
            text: "That's completely fine. Is there anything else you'd like to chat about instead?",
            timestamp: new Date()
          }]);
          
          // Stay at stage 2 for continued conversation
          setIsTyping(false);
        }
      }, 1000);
    }
    else {
      // If conversation is complete but user continues chatting
      setTimeout(() => {
        setMessages(prev => [...prev, {
          sender: 'bot',
          text: "I hope you're enjoying the music! Is there anything else you'd like to share about how you're feeling?",
          timestamp: new Date()
        }]);
        
        // Reset to stage 0 to restart the mood analysis process
        setConversationStage(0);
        setIsTyping(false);
      }, 1000);
    }
  };
  // Generate appropriate bot responses based on detected mood
  const generateBotResponses = (analysis) => {
    const { primaryMood, secondaryMoods, intensity, matches } = analysis;
    
    // First message acknowledges how they're feeling based on intensity and context
    let acknowledgment = '';
    const intensityWord = intensity > 80 ? 'very ' : intensity > 50 ? 'quite ' : '';
    
    // Get keywords that led to mood detection
    const matchedKeywords = matches && matches.length > 0 
      ? matches.map(m => m.keyword)
          .filter(keyword => keyword.length > 1 && !keyword.startsWith('not ')) // Filter out negations and single letters
          .slice(0, 2)
          .join('" and "')
      : '';
    
    // More personalized first responses based on mood type
    switch(primaryMood) {
      case 'happy':
      case 'excited':
      case 'euphoric':
        acknowledgment = `That's wonderful! I can tell you're feeling ${intensityWord}${primaryMood} today. Those positive vibes really come through in your message!`;
        break;
      case 'sad':
      case 'depressed':
      case 'heartbroken':
      case 'grief':
        acknowledgment = `I'm sorry to hear you're feeling ${intensityWord}${primaryMood}. That can be really difficult to deal with, and I appreciate you sharing that with me.`;
        break;
      case 'anxious':
      case 'overwhelmed':
      case 'restless':
        acknowledgment = `I understand that feeling ${intensityWord}${primaryMood} can be challenging. It's completely normal to feel this way sometimes, and acknowledging it is an important first step.`;
        break;
      case 'angry':
      case 'furious':
      case 'resentful':
        acknowledgment = `I see you're feeling ${intensityWord}${primaryMood}. Sometimes expressing that energy can be healthy, and music can be a great outlet.`;
        break;
      case 'calm':
      case 'mindful':
      case 'relieved':
        acknowledgment = `It sounds like you're in a ${intensityWord}${primaryMood} state of mind. That's a wonderful place to be - peaceful and centered.`;
        break;
      case 'energetic':
      case 'motivated':
      case 'powerful':
        acknowledgment = `I'm picking up on your ${intensityWord}${primaryMood} state! That dynamic energy can be really productive and inspiring.`;
        break;
      case 'focused':
      case 'study':
      case 'productive':
        acknowledgment = `I can sense that you're feeling ${intensityWord}${primaryMood} right now. That's a great mindset for accomplishing your goals.`;
        break;
      case 'romantic':
      case 'nostalgic':
      case 'yearning':
        acknowledgment = `There's a ${intensityWord}${primaryMood} quality to what you're expressing. Those emotions can connect us deeply to important memories and people.`;
        break;
      default:
        acknowledgment = `I see you're feeling ${intensityWord}${primaryMood}. Thank you for sharing that with me.`;
    }
    
    // Enhanced second message that includes secondary moods and more personalized info
    let secondaryMoodText = '';
    if (secondaryMoods && Object.keys(secondaryMoods).length > 0) {
      const topSecondaryMood = Object.keys(secondaryMoods)[0];
      const secondIntensity = secondaryMoods[topSecondaryMood]?.intensity || 0;
      
      if (secondIntensity > 40) {
        // More conversational responses about secondary moods
        if (['happy', 'excited', 'euphoric', 'grateful'].includes(topSecondaryMood)) {
          secondaryMoodText = ` I also notice underlying feelings of ${topSecondaryMood}ness, which adds a positive dimension to your emotional state.`;
        } else if (['sad', 'depressed', 'melancholic'].includes(topSecondaryMood)) {
          secondaryMoodText = ` I'm also sensing hints of ${topSecondaryMood}ness in what you're expressing. That adds some complexity to how you're feeling.`;
        } else if (['anxious', 'overwhelmed'].includes(topSecondaryMood)) {
          secondaryMoodText = ` There also seems to be an undercurrent of ${topSecondaryMood}ness in your message, which is something many people experience.`;
        } else {
          secondaryMoodText = ` I also notice hints of ${topSecondaryMood} in what you're expressing.`;
        }
      }
    }
    
    // Add context from matched keywords if available
    let keywordContext = '';
    if (matchedKeywords) {
      keywordContext = ` When you used words like "${matchedKeywords}", it helped me understand your emotional state better.`;
    }
    
    // More natural connection to music recommendation
    const musicTransition = [
      "Music can be really powerful for connecting with our emotions.",
      "I find that music can be a wonderful companion for these feelings.",
      "The right music can really complement or help shift your current emotional state.",
      "Music has an amazing ability to match or transform how we feel."
    ];
    
    // Choose a random transition phrase
    const randomTransition = musicTransition[Math.floor(Math.random() * musicTransition.length)];
    
    const suggestion = `${secondaryMoodText}${keywordContext} ${randomTransition}`;
    
    return [acknowledgment, suggestion];
  };

  // Format the timestamp
  const formatTime = (timestamp) => {
    return timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  return (
    <div className="mood-chat">
      <div className="chat-header">
        <div className="chat-title">
          <span className="chat-icon"></span> Moodify Chat
        </div>
        <div className="chat-subtitle">Talk about your feelings</div>
      </div>
      
      <div className="chat-messages">
        {messages.map((message, index) => (
          <div 
            key={index} 
            className={`message ${message.sender === 'user' ? 'user-message' : 'bot-message'}`}
          >
            <div className="message-text">{message.text}</div>
            <div className="message-time">{formatTime(message.timestamp)}</div>
          </div>
        ))}
        
        {isTyping && (
          <div className="message bot-message typing">
            <div className="typing-indicator">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        )}
        <div ref={messagesEndRef} />
      </div>
      
      <form className="chat-input-form" onSubmit={handleUserMessageSend}>
        <input 
          type="text" 
          value={userInput}
          onChange={(e) => setUserInput(e.target.value)}
          placeholder={isTyping ? "Moodify is typing..." : "Tell me how you're feeling..."}
          disabled={isTyping}
        />
        <button 
          type="submit" 
          disabled={isTyping || !userInput.trim()}
          className="send-button"
          title="Send message"
        >
          <span className="send-icon">
            <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M22 2L11 13" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <path d="M22 2L15 22L11 13L2 9L22 2Z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </span>
          {!isTyping && <span className="send-text">Send</span>}
        </button>
      </form>
    </div>
  );
}

export default MoodChat;
