import React, { useState, useEffect } from 'react';
import './MoodInput.css';

function MoodInput({ onMoodSubmit }) {
  const [emojiValue, setEmojiValue] = useState(5); // Middle value on scale of 0-10
  
  // Enhanced emoji mapping to match our granular mood categories
  const emojiMap = [
    "", // 0 - Depressed
    "", // 1 - Sad
    "", // 2 - Anxious
    "", // 3 - Melancholic
    "", // 4 - Calm
    "", // 5 - Soothing
    "", // 6 - Focused
    "", // 7 - Happy
    "", // 8 - Excited
    "", // 9 - Euphoric
    ""  // 10 - Furious
  ];

  // Mapping from emoji position to mood name for tooltip
  const moodLabelMap = [
    'Depressed', 'Sad', 'Anxious', 'Melancholic', 'Calm',
    'Soothing', 'Focused', 'Happy', 'Excited', 'Euphoric', 'Furious'
  ];
  const handleEmojiSubmit = () => {
    onMoodSubmit(emojiValue, 'emoji');
  };
  
  // Auto-select a default emoji on component mount for better UX
  useEffect(() => {
    setEmojiValue(5); // Default to "Soothing" mood
  }, []);

  return (
    <div className="mood-input">
      <h2 className="emoji-header">How are you feeling right now?</h2>      <div className="emoji-input-form">
        <label htmlFor="mood-emoji">Move the slider to match your current mood</label>
        <div className="emoji-slider-container">
          <div className="emoji-label" title="Depressed">{emojiMap[0]}</div>
          <input
            type="range"
            id="mood-emoji"
            min="0"
            max="10"
            value={emojiValue}
            onChange={(e) => setEmojiValue(parseInt(e.target.value))}
            className="emoji-slider"
          />
          <div className="emoji-label" title="Furious">{emojiMap[10]}</div>
        </div>
        <div className="current-emoji">
          {emojiMap[emojiValue]}
        </div>
        <div className="mood-name">{moodLabelMap[emojiValue]}</div>
        <button onClick={handleEmojiSubmit}>Find Music</button>
      </div>
    </div>
  );
}

export default MoodInput;
