import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import MoodInput from './components/MoodInput';
import MoodChat from './components/MoodChat';
import MoodMessage from './components/MoodMessage';
import PlaylistRecommendation from './components/PlaylistRecommendation';
import SpotifyLoginButton from './components/SpotifyLoginButton';
import SpotifyCallback from './components/SpotifyCallback';
import UserProfile from './components/UserProfile';
import LocalDevAuth from './components/LocalDevAuth';
import { detectMood } from './utils/moodDetector';
import spotifyService from './services/spotifyService';

function App() {
  const [currentMood, setCurrentMood] = useState(null);
  const [fullMoodAnalysis, setFullMoodAnalysis] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isCuratingPlaylists, setIsCuratingPlaylists] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [inputMode, setInputMode] = useState('emoji'); // 'emoji' for emoji slider, 'chat' for MoodChat

  useEffect(() => {
    // Check if user is logged in on component mount
    const checkAuthStatus = () => {
      const loggedIn = spotifyService.isLoggedIn();
      setIsAuthenticated(loggedIn);
    };

    checkAuthStatus();
    
    // Set up an interval to periodically check auth status
    const intervalId = setInterval(checkAuthStatus, 60000); // check every minute
    
    return () => clearInterval(intervalId);
  }, []);  const handleMoodSubmission = async (input, inputType, fullAnalysis = null) => {
    setIsLoading(true);
    
    // If we already have a full analysis (from chat), use it directly
    if (fullAnalysis) {
      setFullMoodAnalysis(fullAnalysis);
      setCurrentMood(fullAnalysis.primaryMood);
      setIsLoading(false);
      
      // For chat input, show curating playlists animation immediately
      // since the chat component now controls the timing of showing playlists
      if (inputType === 'chat') {
        // Show the curating playlists message with animation
        setIsCuratingPlaylists(true);
        
        // Allow time for the curating message to be visible
        setTimeout(() => {
          setIsCuratingPlaylists(false);
        }, 3000); // Curating animation time
      }
      return;
    }
    
    // Detect mood based on input
    try {
      const detectedMood = await detectMood(input, inputType);
      
      // For emoji input, we don't have full analysis, just the mood string
      if (inputType === 'emoji') {
        setCurrentMood(detectedMood);
        setFullMoodAnalysis(null);
      } else {
        // For text input, we should have full analysis (future-proofing)
        if (typeof detectedMood === 'object') {
          setFullMoodAnalysis(detectedMood);
          setCurrentMood(detectedMood.primaryMood);
        } else {
          setCurrentMood(detectedMood);
          setFullMoodAnalysis(null);
        }
      }
    } catch (error) {
      console.error("Error detecting mood:", error);
      alert("Sorry, there was an issue detecting your mood. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };
  
  // Handle switching input modes - clear mood results when changing input methods
  const handleInputModeChange = (mode) => {
    if (mode !== inputMode) {
      setInputMode(mode);
      // Optionally reset the mood results when switching modes
      // Comment out the line below if you want to preserve results when switching
      // setCurrentMood(null);
    }
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
  };

  // Main application content
  const AppContent = () => (
    <div className="App">
      <div className="development-mode-banner">
         This app is in Spotify development mode. To use Spotify integration, feel free to ping me directly and I'll add your email to the whitelist right away!
        <a href="nihalpardeshi12344@gmail.com" className="contact-link">Contact me here</a> for access.
      </div>
      
      <header className="App-header">
        <div className="logo-container">
          <img 
            src={`${process.env.PUBLIC_URL}/images/moodify-logo.png`} 
            alt="Moodify Logo" 
            className="logo" 
          />
        </div>
        <h1>Moodify</h1>
        <p className="App-subtitle">Music recommendations based on your mood</p>
          {isAuthenticated ? (
          <UserProfile onLogout={handleLogout} />
        ) : (
          <SpotifyLoginButton />
        )}
      </header>
      
      {!isAuthenticated && process.env.NODE_ENV === 'development' && (
        <LocalDevAuth />
      )}
        <main className="App-main">        <div className="input-mode-selector">
          <button 
            className={`mode-button ${inputMode === 'emoji' ? 'active' : ''}`}
            onClick={() => handleInputModeChange('emoji')}
          >
            <span className="icon"></span> Emoji Mood Slider
          </button>
          <button 
            className={`mode-button ${inputMode === 'chat' ? 'active' : ''}`}
            onClick={() => handleInputModeChange('chat')}
          >
            <span className="icon"></span> Chat Assistant
          </button>
        </div>

        {inputMode === 'emoji' ? (
          <MoodInput onMoodSubmit={handleMoodSubmission} />
        ) : (
          <MoodChat onMoodDetected={handleMoodSubmission} />
        )}
          {isLoading && <div className="loader">Analyzing your mood...</div>}
        
      {isCuratingPlaylists && <div className="loader curating">
          <div className="curating-animation">
            <span className="curating-icon"></span>
            <span className="curating-icon delayed"></span>
            <span className="curating-icon more-delayed"></span>
          </div>
          <div className="curating-text">
            <p>Curating personalized playlists for your emotional state...</p>
            <p className="curating-subtext">Finding music that resonates with how you're feeling</p>
          </div>
        </div>}
        
        {currentMood && !isLoading && !isCuratingPlaylists && (
          <div className="results-container">
            <MoodMessage mood={currentMood} />
            <PlaylistRecommendation mood={currentMood} fullAnalysis={fullMoodAnalysis} />
          </div>
        )}
      </main>
      
      <footer className="App-footer">
        <p> {new Date().getFullYear()} Moodify</p>
        <p className="made-with-love">Made with  by Nihal</p>
      </footer>
    </div>  );
  // More comprehensive check for callback parameters
  const hasCallbackParams = () => {
    // Check in query parameters
    const urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has('code') && urlParams.has('state')) {
      return true;
    }
    
    // Check in hash (for HashRouter)
    if (window.location.hash) {
      // Check if hash contains a question mark
      if (window.location.hash.includes('?')) {
        const hashParams = new URLSearchParams(window.location.hash.substring(window.location.hash.indexOf('?')));
        if (hashParams.has('code') && hashParams.has('state')) {
          return true;
        }
      }
      
      // Check directly in hash (GitHub Pages specific)
      if (window.location.hash.includes('code=') && window.location.hash.includes('state=')) {
        return true;
      }
    }
    
    return false;
  };
  
  return (
    <Router>
      <Routes>
        <Route path="/" element={hasCallbackParams() ? <SpotifyCallback /> : <AppContent />} />
        <Route path="/callback" element={<SpotifyCallback />} /> {/* Keeping this for backward compatibility */}
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    </Router>
  );
}

export default App;
