import Sentiment from 'sentiment';

/**
 * Utility for detecting mood from user input
 * This is a simplified version using basic text matching
 * In a production app, you might use a more sophisticated NLP model
 */

// Enhanced mood categories with more granular emotions and intensity levels
const moodKeywords = {
  // Primary emotions expanded with sub-categories and intensity levels
  
  // ANXIOUS SPECTRUM
  anxious: ['anxious', 'nervous', 'worried', 'stress', 'stressed', 'panic', 'fear', 'scared', 'uneasy', 'apprehensive'],
  overwhelmed: ['overwhelmed', 'swamped', 'buried', 'too much', 'can\'t handle', 'exhausted', 'burnout', 'drowning'],
  restless: ['restless', 'fidgety', 'jittery', 'on edge', 'tense', 'cannot relax', 'unsettled', 'jumpy'],
  
  // CALM SPECTRUM
  calm: ['calm', 'peaceful', 'relaxed', 'serene', 'tranquil', 'content', 'ease', 'meditation', 'quiet', 'still'],
  mindful: ['mindful', 'present', 'aware', 'centered', 'grounded', 'balanced', 'zen', 'harmony'],
  relieved: ['relieved', 'unburdened', 'weight off', 'free from', 'reassured', 'at ease', 'comforted'],
  
  // HAPPY SPECTRUM
  happy: ['happy', 'joy', 'cheerful', 'glad', 'pleased', 'content', 'satisfied'],
  excited: ['excited', 'thrilled', 'eager', 'enthusiastic', 'pumped', 'stoked', 'amped', 'psyched', 'hyped'],
  euphoric: ['euphoric', 'ecstatic', 'elated', 'overjoyed', 'blissful', 'over the moon', 'on cloud nine', 'exhilarated'],
  grateful: ['grateful', 'thankful', 'blessed', 'appreciative', 'fortunate', 'lucky'],
  optimistic: ['optimistic', 'hopeful', 'positive', 'looking forward', 'upbeat', 'bright side'],
  
  // SAD SPECTRUM
  sad: ['sad', 'down', 'unhappy', 'blue', 'upset', 'low'],
  melancholic: ['melancholic', 'wistful', 'nostalgic', 'reflective', 'reminiscing', 'bittersweet'],
  heartbroken: ['heartbroken', 'devastated', 'crushed', 'broken', 'hurt', 'betrayed', 'abandoned', 'rejected'],
  grief: ['grief', 'mourning', 'loss', 'despair', 'bereft', 'sorrow', 'misery', 'anguish'],
  lonely: ['lonely', 'alone', 'isolated', 'solitary', 'abandoned', 'disconnected', 'misunderstood'],
  depressed: ['depressed', 'hopeless', 'empty', 'numb', 'worthless', 'pointless', 'miserable', 'gloomy'],
  
  // ENERGETIC SPECTRUM
  energetic: ['energy', 'energetic', 'active', 'vigorous', 'lively', 'dynamic', 'vibrant'],
  motivated: ['motivated', 'driven', 'ambitious', 'determined', 'inspired', 'productive', 'proactive'],
  powerful: ['powerful', 'strong', 'unstoppable', 'invincible', 'mighty', 'confident', 'capable'],
  
  // FOCUS SPECTRUM
  focused: ['focused', 'attentive', 'concentrate', 'mindful', 'alert', 'sharp', 'undistracted', 'locked in'],
  study: ['study', 'homework', 'learning', 'reading', 'exam', 'test', 'academic', 'school', 'concentrate'],
  productive: ['productive', 'efficient', 'effective', 'accomplishing', 'achieving', 'getting things done'],
  
  // SOOTHING SPECTRUM
  soothing: ['soothe', 'soothing', 'comfort', 'gentle', 'soft', 'relax', 'cozy'],
  sleepy: ['sleepy', 'tired', 'drowsy', 'exhausted', 'fatigued', 'ready for bed', 'need sleep', 'sluggish', 'yawn'],
  dreamy: ['dreamy', 'fantasy', 'imagination', 'daydream', 'enchanted', 'magical', 'whimsical', 'starry-eyed'],
  
  // ANGRY SPECTRUM
  angry: ['angry', 'mad', 'frustrated', 'upset', 'annoyed', 'irritated'],
  furious: ['furious', 'rage', 'outrage', 'enraged', 'livid', 'seething', 'fuming'],
  resentful: ['resentful', 'bitter', 'grudge', 'hate', 'vengeful', 'vindictive', 'spiteful', 'contempt'],
  
  // LOVE SPECTRUM
  romantic: ['love', 'romantic', 'passion', 'affection', 'enamored', 'smitten', 'adoration', 'intimate', 'crush'],
  nostalgic: ['nostalgic', 'memories', 'remember', 'childhood', 'past', 'reminisce', 'good old days', 'throwback'],
  yearning: ['yearning', 'longing', 'desire', 'crave', 'miss', 'want', 'wish', 'pine'],
  
  // CREATIVE SPECTRUM
  creative: ['creative', 'artistic', 'inspired', 'imagination', 'inventive', 'innovative', 'original', 'expressive'],
  curious: ['curious', 'inquisitive', 'wondering', 'interested', 'fascinated', 'intrigued', 'exploring']
};

// Map emoji slider values to moods with more granular emotions
const emojiMoodMap = [
  'depressed',   // 0 - Very negative emotion
  'sad',         // 1 - Sad
  'anxious',     // 2 - Anxious/worried
  'melancholic', // 3 - Reflective sadness
  'calm',        // 4 - Peaceful
  'soothing',    // 5 - Relaxing
  'focused',     // 6 - Concentration
  'happy',       // 7 - Content/happy
  'excited',     // 8 - Excited
  'euphoric',    // 9 - Very happy
  'furious'      // 10 - Intense emotion
];

/**
 * Enhanced mood detection from text input with support for multiple emotions,
 * intensity analysis, and context sensitivity using NLP techniques
 * 
 * @param {string} text - User input text
 * @returns {Object} - Object containing primary mood and secondary moods with scores
 */
const detectMoodFromText = (text) => {
  // Initialize the sentiment analyzer
  const sentiment = new Sentiment();
  
  // Convert to lowercase and normalize text
  const lowercaseText = text.toLowerCase()
    .replace(/[.,\/#!$%\^&\*;:{}=\-_`~()]/g, ' ') // Replace punctuation with spaces
    .replace(/\s{2,}/g, ' ') // Replace multiple spaces with single space
    .trim();
  
  // Words that increase intensity when they appear before keywords
  const intensifiers = [
    'very', 'really', 'extremely', 'incredibly', 'totally',
    'absolutely', 'completely', 'utterly', 'deeply', 'highly', 
    'so', 'quite', 'rather', 'particularly', 'especially',
    'surprisingly', 'exceedingly', 'unusually', 'notably'
  ];
  
  // Words that negate emotions
  const negators = [
    'not', 'don\'t', 'doesn\'t', 'didn\'t', 'won\'t', 'wouldn\'t',
    'can\'t', 'cannot', 'never', 'no', 'none', 'nothing', 'neither'
  ];
  
  // Split text into words and word pairs to analyze context
  const words = lowercaseText.split(' ');
  const wordPairs = [];
  const wordTriples = [];
  
  // Create pairs of consecutive words for better context
  for (let i = 0; i < words.length - 1; i++) {
    wordPairs.push(words[i] + ' ' + words[i+1]);
    if (i < words.length - 2) {
      wordTriples.push(words[i] + ' ' + words[i+1] + ' ' + words[i+2]);
    }
  }
  
  // Score each mood category
  const moodScores = {};
  const allMoodMatches = {};
  
  Object.entries(moodKeywords).forEach(([mood, keywords]) => {
    moodScores[mood] = 0;
    allMoodMatches[mood] = [];
    
    // Check for full phrases first (highest weight)
    wordTriples.forEach(triple => {
      keywords.forEach(keyword => {
        if (triple.includes(keyword)) {
          // Check if there's an intensifier
          const hasIntensifier = intensifiers.some(intensifier => 
            triple.startsWith(intensifier + ' ') || 
            triple.includes(' ' + intensifier + ' ')
          );
          
          // Check for negation
          const hasNegator = negators.some(negator => 
            triple.startsWith(negator + ' ') || 
            triple.includes(' ' + negator + ' ')
          );
          
          if (!hasNegator) {
            const weight = hasIntensifier ? 5 : 3;
            moodScores[mood] += weight;
            allMoodMatches[mood].push({
              keyword,
              context: triple,
              weight
            });
          } else {
            // If negated, reduce the score slightly (but don't make it negative)
            moodScores[mood] = Math.max(0, moodScores[mood] - 1);
          }
        }
      });
    });
    
    // Check for word pairs (medium weight)
    wordPairs.forEach(pair => {
      keywords.forEach(keyword => {
        if (pair.includes(keyword)) {
          // Check if there's an intensifier
          const hasIntensifier = intensifiers.some(intensifier => 
            pair.startsWith(intensifier + ' ') || 
            pair.includes(' ' + intensifier + ' ')
          );
          
          // Check for negation
          const hasNegator = negators.some(negator => 
            pair.startsWith(negator + ' ') || 
            pair.includes(' ' + negator + ' ')
          );
          
          if (!hasNegator) {
            const weight = hasIntensifier ? 3 : 2;
            moodScores[mood] += weight;
            allMoodMatches[mood].push({
              keyword,
              context: pair,
              weight
            });
          } else {
            // If negated, look for opposite emotions
            Object.entries(moodKeywords).forEach(([oppMood, _]) => {
              if (isOppositeEmotion(mood, oppMood)) {
                moodScores[oppMood] += 1;
                allMoodMatches[oppMood].push({
                  keyword: "not " + keyword,
                  context: pair,
                  weight: 1
                });
              }
            });
          }
        }
      });
    });
    
    // Check for individual words (lowest weight)
    words.forEach((word, index) => {
      // Skip if this word is a negator
      if (negators.includes(word)) return;
      
      keywords.forEach(keyword => {
        // For multi-word keywords, skip individual word matching
        if (keyword.includes(' ')) return;
        
        if (word === keyword) {
          // Check if preceded by a negator
          const isNegated = index > 0 && negators.includes(words[index-1]);
          
          // Check if preceded by an intensifier
          const isIntensified = index > 0 && intensifiers.includes(words[index-1]);
          
          if (!isNegated) {
            const weight = isIntensified ? 2 : 1;
            moodScores[mood] += weight;
            allMoodMatches[mood].push({
              keyword,
              context: isIntensified ? words[index-1] + ' ' + word : word,
              weight
            });
          } else {
            // If negated, look for opposite emotions
            Object.entries(moodKeywords).forEach(([oppMood, _]) => {
              if (isOppositeEmotion(mood, oppMood)) {
                moodScores[oppMood] += 1;
                allMoodMatches[oppMood].push({
                  keyword: "not " + keyword,
                  context: words[index-1] + ' ' + word,
                  weight: 1
                });
              }
            });
          }
        }
      });
    });
  });
    // Run sentiment analysis on the input text
  const sentimentResult = sentiment.analyze(text);
  
  // Log the sentiment analysis results for debugging
  console.log('Sentiment Analysis:', sentimentResult);
  
  // Use sentiment score to bias toward positive or negative emotions
  const sentimentScore = sentimentResult.score;
  const sentimentComparative = sentimentResult.comparative;
  
  // Words with high sentiment impact from the analysis
  const significantWords = [
    ...sentimentResult.positive,
    ...sentimentResult.negative
  ];

  // Emotion groups based on sentiment
  const positiveEmotions = ['happy', 'excited', 'euphoric', 'grateful', 'optimistic', 'energetic', 'motivated', 'powerful', 'romantic', 'creative'];
  const negativeEmotions = ['sad', 'anxious', 'angry', 'depressed', 'lonely', 'heartbroken', 'grief', 'resentful', 'furious', 'overwhelmed'];
  const neutralEmotions = ['calm', 'focused', 'melancholic', 'mindful', 'nostalgic', 'soothing', 'relieved', 'sleepy', 'dreamy', 'curious'];
  
  // Enhanced sentimentScore handling
  
  // First check for negation of emotion words
  // The pattern "not happy", "not feeling well", etc.
  const negatedPositiveKeywords = [];
  const negatedNegativeKeywords = [];
  
  // Check for negated positive words
  positiveEmotions.forEach(emotion => {
    // Check if any keywords for this emotion are negated in the text
    if (moodKeywords[emotion]) {
      moodKeywords[emotion].forEach(keyword => {
        const negationPatterns = negators.map(negator => `${negator} ${keyword}`);
        
        // Check if any negation pattern exists in the text
        if (negationPatterns.some(pattern => lowercaseText.includes(pattern))) {
          negatedPositiveKeywords.push(keyword);
          // Significantly reduce the score for this emotion
          moodScores[emotion] = Math.max(0, moodScores[emotion] - 5);
          // Increase scores for opposite emotions
          Object.entries(moodKeywords).forEach(([oppMood, _]) => {
            if (isOppositeEmotion(emotion, oppMood)) {
              moodScores[oppMood] = (moodScores[oppMood] || 0) + 3;
            }
          });
        }
      });
    }
  });
  
  // Give stronger weight to sentiment analysis
  if (sentimentScore > 3) {
    // Strong positive sentiment
    positiveEmotions.forEach(emotion => {
      if (moodScores[emotion]) moodScores[emotion] += 3;
    });
    // Reduce negative emotions more aggressively
    negativeEmotions.forEach(emotion => {
      if (moodScores[emotion]) moodScores[emotion] = Math.max(0, moodScores[emotion] - 2);
    });
  } else if (sentimentScore > 0) {
    // Mild positive sentiment
    positiveEmotions.forEach(emotion => {
      if (moodScores[emotion]) moodScores[emotion] += 2;
    });
  } else if (sentimentScore < -3) {
    // Strong negative sentiment
    negativeEmotions.forEach(emotion => {
      if (moodScores[emotion]) moodScores[emotion] += 3;
    });
    // Reduce positive emotions more aggressively
    positiveEmotions.forEach(emotion => {
      if (moodScores[emotion]) moodScores[emotion] = Math.max(0, moodScores[emotion] - 2);
    });
  } else if (sentimentScore < 0) {
    // Mild negative sentiment
    negativeEmotions.forEach(emotion => {
      if (moodScores[emotion]) moodScores[emotion] += 2;
    });
  }
  
  // If the text has very little emotional content (close to neutral)
  if (Math.abs(sentimentComparative) < 0.1 && text.length > 10) {
    neutralEmotions.forEach(emotion => {
      if (moodScores[emotion]) moodScores[emotion] += 1;
    });
  }
  
  // Find primary mood (highest score)
  let primaryMood = 'happy'; // Default
  let highestScore = 0;
  
  Object.entries(moodScores).forEach(([mood, score]) => {
    if (score > highestScore) {
      highestScore = score;
      primaryMood = mood;
    }
  });
  
  // Only consider moods with non-zero scores
  const secondaryMoods = Object.entries(moodScores)
    .filter(([mood, score]) => score > 0 && mood !== primaryMood)
    .sort((a, b) => b[1] - a[1])
    .slice(0, 3) // Take top 3 secondary moods
    .reduce((obj, [mood, score]) => {
      obj[mood] = {
        score,
        intensity: calculateIntensity(score, highestScore),
        matches: allMoodMatches[mood]
      };
      return obj;
    }, {});
  
  return {
    primaryMood,
    score: highestScore,
    intensity: calculateIntensity(highestScore, highestScore), // Will always be 100%
    matches: allMoodMatches[primaryMood],
    secondaryMoods
  };
};

/**
 * Calculate intensity of the emotion as percentage of highest score
 */
const calculateIntensity = (score, highestScore) => {
  if (highestScore === 0) return 0;
  return Math.round((score / highestScore) * 100);
};

/**
 * Determine if two emotions are opposites (for negation handling)
 */
const isOppositeEmotion = (mood1, mood2) => {
  const opposites = {
    'happy': ['sad', 'depressed', 'melancholic', 'heartbroken', 'grief', 'lonely'],
    'sad': ['happy', 'excited', 'euphoric', 'grateful', 'optimistic'],
    'anxious': ['calm', 'relieved', 'focused', 'mindful'],
    'calm': ['anxious', 'restless', 'furious', 'angry', 'overwhelmed'],
    'energetic': ['sleepy', 'tired', 'depressed'],
    'angry': ['calm', 'happy', 'peaceful', 'relieved'],
    'excited': ['bored', 'sad', 'melancholic'],
    'euphoric': ['depressed', 'sad', 'grief'],
    'depressed': ['happy', 'excited', 'euphoric', 'energetic'],
    'motivated': ['tired', 'bored', 'depressed'],
    'focused': ['anxious', 'overwhelmed', 'distracted'],
    'overwhelmed': ['calm', 'focused', 'relieved'],
    'grateful': ['resentful', 'bitter'],
    'optimistic': ['depressed', 'pessimistic', 'sad']
  };
  
  return opposites[mood1]?.includes(mood2) || opposites[mood2]?.includes(mood1);
};

/**
 * Detects mood from emoji slider value
 * @param {number} value - Emoji slider value (0-10)
 * @returns {string} - Detected mood category
 */
const detectMoodFromEmoji = (value) => {
  // Ensure value is within bounds
  const safeValue = Math.max(0, Math.min(10, value));
  return emojiMoodMap[safeValue];
};

/**
 * Main mood detection function - enhanced with multi-mood detection
 * and emotional intensity analysis
 * 
 * @param {string|number} input - Either text input or emoji slider value
 * @param {string} inputType - Either 'text' or 'emoji'
 * @returns {Promise<Object>} - Promise resolving to full mood analysis or simple mood string
 */
export const detectMood = async (input, inputType) => {
  // Add a small delay to simulate processing
  return new Promise((resolve) => {
    setTimeout(() => {
      if (inputType === 'text') {
        // For text input, provide the full analysis object
        const moodAnalysis = detectMoodFromText(input);
        
        // Log detailed mood analysis for debugging
        console.log('Mood Analysis:', moodAnalysis);
        
        // Return the full analysis object instead of just the primary mood
        // This allows for more personalized recommendations
        resolve(moodAnalysis);
      } else {
        // For emoji input, create a simulated analysis object to ensure we get personalized playlists
        // instead of just returning the mood string
        const moodName = detectMoodFromEmoji(input);
        const intensity = Math.abs(input - 5) * 20; // Calculate intensity based on distance from neutral (5)
        
        // Create a mock analysis object with intensity and some secondary moods
        const mockAnalysis = {
          primaryMood: moodName,
          score: 10,
          intensity: intensity,
          secondaryMoods: {}
        };
        
        // Add some secondary moods based on the emoji position to make recommendations more accurate
        if (input <= 3) { // Lower values - more negative emotions
          mockAnalysis.secondaryMoods.melancholic = { score: 5, intensity: 50 };
          if (input <= 1) mockAnalysis.secondaryMoods.depressed = { score: 3, intensity: 30 };
        } else if (input >= 7) { // Higher values - more positive emotions
          mockAnalysis.secondaryMoods.excited = { score: 5, intensity: 50 };
          if (input >= 9) mockAnalysis.secondaryMoods.euphoric = { score: 3, intensity: 30 };
        } else { // Middle values - more balanced emotions
          mockAnalysis.secondaryMoods.calm = { score: 5, intensity: 50 };
          mockAnalysis.secondaryMoods.focused = { score: 3, intensity: 30 };
        }
        
        console.log('Generated mood analysis for emoji input:', mockAnalysis);
        resolve(mockAnalysis);
      }
    }, 1000);
  });
};

/**
 * Get the full mood analysis from text without resolving to a single mood
 * This can be used in the future for more advanced playlist recommendations
 * 
 * @param {string} text - User input text
 * @returns {Object} - Complete mood analysis
 */
export const getFullMoodAnalysis = (text) => {
  return detectMoodFromText(text);
};

/**
 * Analyze a phrase for negations and double-negations
 * This helps with more accurately detecting phrases like "not happy" or "not feeling sad"
 * 
 * @param {string} phrase - Phrase to analyze
 * @param {Array} negators - List of negation words
 * @return {Object} - Object with isNegated flag and negationCount
 */
const analyzeNegation = (phrase, negators) => {
  if (!phrase) return { isNegated: false, negationCount: 0 };
  
  // Convert phrase to array of words
  const words = phrase.toLowerCase().split(' ');
  let negationCount = 0;
  
  // Count how many negators appear in the phrase
  words.forEach(word => {
    if (negators.includes(word)) negationCount++;
  });
  
  // Even number of negations cancel each other out (double negative = positive)
  // Odd number of negations = negative
  return {
    isNegated: negationCount % 2 === 1,
    negationCount
  };
};
